.wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  margin-left: -0.5rem;
  //margin: 0 auto;
  box-shadow: 0 5px 15px 0px rgba(14, 197, 23, 0.4);
  width: 270px;
  height: auto;
  animation: tracking-in-expand 3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  @media only screen and (min-width: 768px) {
    width: 290px;
  }
}
.title {
  color: black;
}
.subtitle {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  padding: 0.5rem 0;
  color: #545454;
}
.bio {
  color: black;
  text-align: justify;
  margin-bottom: 1rem;
}

.size--l {
  font-size: 3rem;
  font-weight: 900;
}

.size--m {
  font-size: 2rem;
  font-weight: 700;
}

.highlight {
  display: flex;
  flex-direction: column-reverse;
  max-width: 300px;
  padding: 0 1rem 2rem 1rem;
}

.highlight:last-child {
  margin-right: auto;
}

.highlight__header {
  display: flex;
  flex-direction: column;
}

.highlight__figure {
  position: relative;
}

.highlight__img {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 8rem;
}

.highlight__caption {
  position: absolute;
  bottom: 1rem;
  right: -0.5rem;
  padding: 0.5rem;
  color: #fff;
  background: #545454;
  border-radius: 2rem;
}

.highlight__footer {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: space-between;
  order: -1;
  padding: 1rem 0;
  border-bottom: 1px solid grey;
}
.followers__wrapper {
  display: flex;
  align-items: flex-start;
  flex-flow: row;
  gap: 1rem;
}
.highlight__followers {
  font-size: 0.75rem;
  font-weight: bold;
  color: black;
}

.highlight__location {
  margin-top: 1rem;
  font-size: 0.75rem;
  font-weight: bold;
  color: black;
}

.highlight__email {
  font-size: 0.75rem;
  font-weight: bold;
  color: #084544;
  text-decoration: none;
}
@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
